import React, { Fragment } from 'react';
import { gql } from 'apollo-boost';
import { Query } from 'react-apollo';
import { Link } from 'react-router-dom';
import classNames from 'classnames';

const LAUNCH_QUERY = gql`
  query LaunchQuery($flight_number: Int!) {
    launch(flight_number: $flight_number) {
      flight_number
      mission_name
      launch_year
      launch_success
      launch_date_local
      rocket {
        rocket_id
        rocket_name
        rocket_type
      }
    }
  }
`;

const Launch = (props) => {
  let { flight_number } = props.match.params;
  flight_number = parseInt(flight_number);

  return (
    <Fragment>
      <Query query={LAUNCH_QUERY} variables={{ flight_number }}>
        {({ loading, error, data }) => {
          if (loading)
            return (
              <div
                style={{ height: '250px' }}
                className="d-flex justify-content-center align-items-center"
              >
                <div className="spinner-border text-info"></div>
              </div>
            );
          if (error) console.log(error);

          return (
            <div>
              <h1 className="display-4 my-3">
                <span className="text-secondary">Mission: </span>
                {data.launch.mission_name}
              </h1>
              <h4 className="mb-3">Launch Details</h4>
              <ul className="list-group">
                <li className="list-group-item">
                  Flight Number:{' '}
                  <span className="text-info">{data.launch.flight_number}</span>
                </li>
                <li className="list-group-item">
                  Launch Year:{' '}
                  <span className="text-info">{data.launch.launch_year}</span>
                </li>
                <li className="list-group-item">
                  Launch Successful:{' '}
                  <span
                    className={classNames({
                      'text-success': data.launch.launch_success,
                      'text-danger': !data.launch.launch_success,
                    })}
                  >
                    {data.launch.launch_success ? 'Yes' : 'No'}
                  </span>
                </li>
              </ul>
              <h4 className="my-3">Rocket Details</h4>
              <ul className="list-group">
                <li className="list-group-item">
                  Rocket ID:{' '}
                  <span className="text-info">
                    {data.launch.rocket.rocket_id}
                  </span>
                </li>
                <li className="list-group-item">
                  Rocket Name:{' '}
                  <span className="text-info">
                    {data.launch.rocket.rocket_name}
                  </span>
                </li>
                <li className="list-group-item">
                  Rocket Type:{' '}
                  <span className="text-info">
                    {data.launch.rocket.rocket_type}
                  </span>
                </li>
              </ul>
              <hr />
              <Link to="/" className="btn btn-outline-info btn-block">Back</Link>
            </div>
          );
        }}
      </Query>
    </Fragment>
  );
};

export default Launch;
